.auth_container{
    display: flex;
    flex-direction: column;
    margin: 8% auto 0;
    background: #fff;
    border-radius: 15px;
    opacity: 1;
    padding: 40px;
    width: 395px;

    h1{
        font-size: 1.4em !important;
    }

    p{
        font-size: 0.9em !important;
    }

    .help, .is-danger{
        font-size: 0.7em !important;
    }

    .login_prompt{
        font-size: 0.8em !important;
    }

    .button{
        width: 100%;
    }

    .auth_link{
        color: #333;
        text-decoration: underline;
    }

    .auth_link_primary{
        color: #7957d5;
        text-decoration: underline;
    }

    .benefit{
        padding-bottom: 7px;
        font-size: 0.9em;

        i{
            color: #714dd2;
            padding-right: 15px;
        }

    }

    .auth_initial{
        display: flex;
        flex-direction: column;
    }

    .auth_form_container{
        flex-direction: column;
        display: flex;

        .chevron_back{
            padding-right: 12px;
            color: #333;
            i{
                padding: 3px 5px;
                font-size: 18px;
                &:hover{
                    cursor: pointer;
                }
            }
        }

        .label{
            margin-bottom: 0;
            font-size: 0.9em;
        }

    }

} // End Auth Container


.login_form_container{
    margin-top: 0.5em;
}


.auth_button{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 40px !important;
    background-color: rgba(64,87,109,.07) !important;
    transition: background-color .1s linear,border-color .1s linear,color .1s linear;
    margin-bottom: 0.75em;
    border: 2px solid transparent !important;
    border-radius: 4px;
    cursor: pointer;
    padding: 0 6px;
    font-size: 0.9em !important;

    &:hover{
        background-color: rgba(57,76,96,.15) !important;
    }

    .icon_container{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        transition: opacity .15s ease-in-out;

        i{
            display: inline-block;
            vertical-align: text-bottom;
            box-sizing: border-box;
            width: 20px;
            height: 20px;
            font-size: 20px;
        }
    }

    .auth_text{
        display: inline-block;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-weight: 600;
        flex: 1 1;
        transition: opacity .15s ease-in-out;
        padding: 0 8px;
        margin: auto;
    }

}

.email_signup_btn{
    font-weight: 600;
    font-size: 0.9em !important;
    height: 40px;
}
