.ui-resizable-handle {
    //width: 7px;
    //height: 7px;
    background-color: #ffffff;
    //border: 1px solid #000000;
    box-shadow: 0 0 5px 1px rgba(57,76,96,.15),0 0 0 1px rgba(53,71,90,.2);
    position: absolute;
    display: block;
}

.ui-resizable-n{
    top: -4px;
    left: 50%;
    width: 14px;
    height: 5px;
    &:hover{
        cursor: ns-resize;
    }
}

.ui-resizable-e {
    right:-4px;
    top:50%;
    width: 5px;
    height: 14px;
    &:hover{
        cursor: ew-resize;
    }
}

.ui-resizable-s {
    bottom: -4px;
    left: 50%;
    width: 14px;
    height: 5px;
    &:hover{
        cursor: ns-resize;
    }
    &:after{
        border: 1px solid #000;
    }
}

.ui-resizable-w {
    left:-4px;
    top:50%;
    width: 5px;
    height: 14px;
    &:hover{
        cursor: ew-resize;
    }
}

.ui-resizable-nw {
    left: -6px;
    top: -6px;
    width:12px;
    height: 12px;
    border-radius: 100%;
    &:hover{
        cursor: nwse-resize;
    }
}

.ui-resizable-ne {
    top: -6px;
    right: -6px;
    width:12px;
    height: 12px;
    border-radius: 100%;
    &:hover{
        cursor: nesw-resize;
    }
}

.ui-resizable-sw {
    bottom: -6px;
    left: -6px;
    width:12px;
    height: 12px;
    border-radius: 100%;
    &:hover{
        cursor: nesw-resize;
    }
}

.ui-resizable-se {
    bottom: -6px;
    right: -6px;
    width:12px;
    height: 12px;
    border-radius: 100%;
    &:hover{
        cursor: nwse-resize;
    }
}


.item-resizable{
    border: 2px solid transparent;

    &:hover{
        border: 2px solid #00c4cc !important;
    }
}

.image_outer{
    position: relative;
    width: 100%;
    //height: 62.5%;

    img{
        width: 100%;
        height: 100% !important;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

}
