
.navbar{
    //background: linear-gradient(to right, #fbc2eb, #a18cd1);
    //background: linear-gradient(to right, #86fde8, #acb6e5);
    //background: linear-gradient(45.34deg, #FBB13C 5.66%, #FF7A72 48.62%, #FF7A72 94.35%);
    //background: linear-gradient(45.34deg, #00d8d8 5.66%, #72AAFF 50.01%, #8A2BE2 94.35%);

    .navbar-item{
        //color: #fff;
        font-weight: 600;
    }

    a.navbar-item:hover{
        background-color: initial !important;
    }

    .profile_image_background_container{
        line-height: 1.5;
        padding: .5rem .75rem .5rem .25rem;
        position: relative;
    }

    .profile_image_container{
        display: flex;
        position: relative;
        padding: .5rem 0.75rem .5rem 0;
        justify-content: center;

        // prevent selection on double click
        -moz-user-select     : none;
        -khtml-user-select   : none;
        -webkit-user-select  : none;
        -o-user-select       : none;
        user-select          : none;

        img{
            border-radius: 100%;
        }

        .profile_image_background{
            background: #4DD2B4;
            display: flex;
            width: 40px;
            height: 40px;
            border-radius: 100%;
            flex-direction: column;
            align-items: center;


            &:hover{
                cursor: pointer;
            }

            span{
                padding-top: 3px;
                text-transform: capitalize;
                font-weight: 600;
                font-size: 22px;
                color: #fff;
            }
        }

        .navbar-dropdown{
            left: initial !important;
            right: 0 !important;

            .navbar-item{
                &:hover{
                    background: #f1f1f1 !important;
                }
            }

        }

    } // End Profile Image Container

    .nav_login_button{
        border: 1px solid #7957d5 !important;
        &:hover{
            background: #7957d5 !important;
            color: #fff !important;
            opacity: 0.92 !important;
        }
    }

} // Navbar End
