body{
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    scrollbar-width: none !important;
}

body::-webkit-scrollbar {
    width: 0 !important;
}

.wrapper{
    flex: 1;
    display: flex;
    padding-left: 0 !important; // for sidebar
    padding-right: 0 !important; // for content
    scrollbar-width: none !important;
}

.wrapper::-webkit-scrollbar {
    width: 0 !important;
}

// rainbow google icon using fontawesome
.fa-google{
    background:
        linear-gradient(to bottom left,transparent 49%,#fbbc05 50%) 0 25%/48% 40%,
        linear-gradient(to top    left,transparent 49%,#fbbc05 50%) 0 75%/48% 40%,

        linear-gradient(-40deg ,transparent 53%,#ea4335 54%),
        linear-gradient( 45deg ,transparent 46%,#4285f4 48%),
        #34a853;
    background-repeat:no-repeat;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    -webkit-text-fill-color: transparent;
}

.fa-facebook-square{
    color: #3b5998;
}

.button.is-primary{
    opacity: 0.92;
    &:hover{
        opacity: 1;
    }
}

.divider{
    margin-top: 1em !important;
    margin-bottom: 1em !important;
    color: #2f4050;
    text-align: center;
    opacity: 0.25;
    font-size: 0.6em !important;
}

.divider:after, .divider:before{
    content: "";
    display: inline-block;
    //width: 15px;
    width: 45%;
    height: 1px;
    margin: 0 4px;
    vertical-align: middle;
    background: #2f4050;
}

.columns{
    margin-left: 0 !important;
    margin-right: 0 !important;
    margin-top: 0 !important;
}

.background_container{
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 100%;

    img{
        width: 100%;
        padding-left: 5px;
        //height: 100%;
        //background-size: cover;
    }
}

.content{
    position: relative;
    overflow: hidden;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 93%;
    float: right;
    padding-bottom: 80px;
    z-index: 10;
}

// layout for auth views - login/register/password etc
.auth_content{
    display: flex;
    flex-grow: 1;
    justify-content: center;
    align-items: center;
}

.container_section{
    display:flex;
    flex-direction: column;
    padding-left: 30px;
    padding-right: 48px;
    padding-top: 30px;
    width: 100%;

    .content_container{
        padding: 0;
        margin-top: 1.5em !important;
        margin-bottom: 4em !important;
    }
}

.container_section_with_breadcrumbs{
    display:flex;
    flex-direction: column;
    padding-left: 48px;
    padding-right: 48px;
    padding-top: 55px;
    width: 100%;

    .content_container{
        padding: 0;
        margin-top: 1.5em !important;
        margin-bottom: 4em !important;
    }
}

.container_section_with_hero{
    display:flex;
    flex-direction: column;
    padding-left: 48px;
    padding-right: 48px;
    padding-top: 75px;
    width: 100%;

    .content_container{
        padding: 0;
        margin-top: 1.5em !important;
        margin-bottom: 4em !important;
    }
}

.breadcrumbs{
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 15px;
    margin-bottom: 15px;
    //border-bottom: 1px solid #111
    //box-shadow: 0 1px 0 rgba(57,76,96,.15);
    box-shadow: 0 1px 0 rgba(103, 128, 159, 0.45);
    background: #fff;
    width: 100%;
    position: fixed;
    top: 57px;
    z-index: 30;
}

.hero_title{

    h1, h2{
        color: #fff !important;
    }

}


