.mockup_prep_container{
    position: relative;
    width: 100%;
    height: 100%;

    .mockup_item{
        img{
            border: 1px solid #f5f5f5;
            box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        }
    }

    .mockup_design{
        display: block;
        position: absolute;
        top: 28%;
        left: 38%;
        width: 240px;

        .mockup_design_image_container{
            width: 100%;
            height: 100%;
            overflow: hidden;
            padding: 6%;
            //padding: 16px;
            //border: 2px solid transparent;
            //
            //&:hover{
            //    border: 2px solid #00c4cc !important;
            //}

            img{
                width: 100%;
                height: 100%;
            }
        }

    }

}


.mockup_design_template_container{

    .template{ // overall container
        padding: 0;
        margin-top: 1.5em !important;
        margin-bottom: 4em !important;

        //&:first-child{
        //    padding-bottom: 0;
        //    margin-bottom: 1.5em;
        //}

        .single_mockup{ // parent container of images within mockup
            padding: 0;
            border: 1px solid #333;
            overflow: hidden;
            position: relative;

            .mockup_item{
                position: absolute;
                top: 0;
                //width: 500px;

                .mockup_item_image_container{
                    width: 100%;
                    height: 100%;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
            } // End Mockup Item

            .mockup_design{
                display: block;
                position: absolute;

                .mockup_design_image_container{
                    width: 100%;
                    height: 100%;
                    overflow: hidden;
                    padding: 6%;

                    img{
                        width: 100%;
                        height: 100%;
                    }
                }

            }

        } // End Single Mockup

    } // End Template

} // End Mockup Design Template Container


