.error_wrapper{
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    background: #fde0e6;

    .error{
        text-align: center;
        position: relative;
        top: -35px;
        padding: 100px 80px;
        border: 1px solid #333;
        border-radius: 100%;
        background: #fff;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    }

    .title{
        font-size: 140px;
        margin-bottom: 7px !important;
    }

    .button{
        margin-top: 20px;
    }

}
