.left_sidebar{

    width: 82px;
    float: left;
    display: flex;
    flex-direction: column;

    .menu-list::-webkit-scrollbar{
        width: 0 !important;
    }

    .menu-list{
        display: inline-block;
        position: fixed;
        width: 82px;
        top: 55px;
        bottom: 0;
        overflow-y: auto;
        max-height: 100vh;
        scrollbar-width: none !important;
        z-index: 20; // overlay breadcrumbs for mobile responsiveness (tablet/phone)
        background: #111;

        .menu-list-item:first-child{
            a{
                padding-top: 25px;
            }
        }

        .menu-list-item{
            //padding: 12px;
            background: #111;

            &:hover{
                cursor: pointer;
            }

            a{
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                justify-content: center;
                align-items: center;
                color: #fff;
                opacity: 0.65;
                padding: 20px 10px;

                &:hover{
                    background: #111;
                    opacity: 1;
                    cursor: pointer;
                }

                i{
                    font-size: 20px;
                }

                span{
                    font-size: 11px;
                    margin-top: 5px;
                    font-family: sans-serif;
                }
            }

        } // End menu list item


        .sidebar_one{
            a{
                &:hover{
                    color: #19f7dd;
                    opacity: 1;
                }
            }

            .active{
                color: #19f7dd;
                opacity: 1;
            }
        }

        .sidebar_two{
