.ui-resizable-handle {
  background-color: #ffffff;
  box-shadow: 0 0 5px 1px rgba(57, 76, 96, 0.15), 0 0 0 1px rgba(53, 71, 90, 0.2);
  position: absolute;
  display: block;
}

.ui-resizable-n {
  top: -4px;
  left: 50%;
  width: 14px;
  height: 5px;
}

.ui-resizable-n:hover {
  cursor: ns-resize;
}

.ui-resizable-e {
  right: -4px;
  top: 50%;
  width: 5px;
  height: 14px;
}

.ui-resizable-e:hover {
  cursor: ew-resize;
}

.ui-resizable-s {
  bottom: -4px;
