  height: 14px;
}

.ui-resizable-w:hover {
  cursor: ew-resize;
}

.ui-resizable-nw {
  left: -6px;
  top: -6px;
  width: 12px;
  height: 12px;
  border-radius: 100%;
}

.ui-resizable-nw:hover {
  cursor: nwse-resize;
}

.ui-resizable-ne {
  top: -6px;
  right: -6px;
  width: 12px;
  height: 12px;
  border-radius: 100%;
}

.ui-resizable-ne:hover {
  cursor: nesw-resize;
}

.ui-resizable-sw {
  bottom: -6px;
  left: -6px;
  width: 12px;
  height: 12px;
  border-radius: 100%;
}

.ui-resizable-sw:hover {
  cursor: nesw-resize;
}

.ui-resizable-se {
